<style lang="less" scoped>
  .el-date-editor--datetimerange.el-input__inner {
    width: 224px;
  }
  .el-date-editor--daterange.el-input__inner {
    width: 224px;
  }
  .el-input {
    width: 200px;
  }
</style>
<template>
  <div>
    <top-header title="我的审批"></top-header>
    <div class="page-container">
      <div class="table-filters">
        <div class="filter-item">
          <p class="item">角色名称</p>
          <el-input v-model="filterCondition.content"
                    clearable
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">流程状态</p>
          <el-select
            v-model="filterCondition.status"
            size="small"
            placeholder="请选择">
            <el-option v-for="(el, key) in statusCode"
                       :key="key"
                       :label="el"
                       :value="parseInt(key)">
            </el-option>
          </el-select>
        </div>
        <div class='search-btn'>
          <el-button type="primary"
                     size="small"
                     icon="el-icon-search"
                     @click="handleSizeChange()">查询
          </el-button>
        </div>
      </div>
      <el-table
        :data="tableData"
        size="mini"
        style="width: 100%">
        <el-table-column
          fixed
          prop="id"
          type="index"
          label="序号"
          width="60">
        </el-table-column>
        <el-table-column
          label="申请内容">
          <template slot-scope="scope">
            <router-link :to="{path: `/process-management/approval/${scope.row.id}`}">
              <el-button size="mini" type="text">{{scope.row.content}}</el-button>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column
          :show-overflow-tooltip="true"
          label="申请权限用户">
          <template slot-scope="scope">
            {{$utils.joinName(filterKey(scope.row.user, 'name'))}}
          </template>
        </el-table-column>
        <el-table-column
          prop="applicant"
          label="申请人">
        </el-table-column>
        <el-table-column
          prop="department_name"
          label="所属部门">
        </el-table-column>
        <el-table-column
          prop="status"
          label="流程状态">
          <template slot-scope="scope">
            <span :style="{color: $utils.checkStatus(scope.row.status, 'color')}">
              {{$utils.checkStatus(scope.row.status)}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注">
        </el-table-column>
        <el-table-column
          label="操作"
          width="90">
          <template slot-scope="scope">
            <el-button size="mini" type="text" :disabled="$utils.isNotEqual(scope.row.status, 1)">
              <kye-confirm
                :disabled="$utils.isNotEqual(scope.row.status, 1)"
                label="通过"
                title="通过权限申请"
                content="确定通过权限申请流程？点击确定后访问权限将自动开通！"
                :callback="handlePass"
                :scope-data="scope.row">
              </kye-confirm>
            </el-button>
            <el-button size="mini" type="text" :disabled="$utils.isNotEqual(scope.row.status, 1)">
              <kye-prompt
                :disabled="$utils.isNotEqual(scope.row.status, 1)"
                label="驳回"
                title="请输入驳回申请权限原因"
                :callback="handleReject"
                :scope-data="scope.row">
              </kye-prompt>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        v-show="cptPagination"
        @size-change="handleSizeChange"
        @current-change="handleCurrentPageChange"
        :page-sizes="page.sizes"
        layout="sizes,prev,pager,next"
        :current-page.sync="page.current"
        :total="page.total"
        prev-text="上一页"
        next-text='下一页'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '../../api'
import TopHeader from '../../components/header'
export default {
  components: {
    TopHeader
  },
  data () {
    return {
      page: {
        total: 0,
        size: 10,
        current: 1,
        sizes: [10, 20, 50, 100]
      },
      tableData: [],
      // 查询条件
      filterCondition: {
        content: '',
        status: 0,
        page: 1,
        per_page: 10
      },
      // 申请流程状态：1 审批中 2 已驳回 3 已通过
      statusCode: {
        0: '不限',
        1: '审批中',
        2: '已驳回',
        3: '已通过'
      }
    }
  },
  computed: {
    // 如果总条数大于默认显示数
    cptPagination: function () {
      return this.page.total >= 10
    }
  },
  created () {
    this.getSearchData()
  },
  methods: {
    getSearchData () {
      api.getApprovalList(this.filterCondition).then(res => {
        this.tableData = res.result.data
        this.page.total = res.result.pagination.total_count
      })
    },
    // 查询按钮 和 列表条数
    handleSizeChange (val) {
      // 如果val为空表示通过查询按钮触发，使用当前选中的条数
      if (val) {
        this.filterCondition.per_page = val
      }
      this.page.current = this.filterCondition.page = 1
      this.getSearchData()
    },
    // 当前页
    handleCurrentPageChange (val) {
      this.filterCondition.page = val
      this.getSearchData()
    },
    handlePass (id) {
      return api.putApprovalId(id).then(res => {
        if (res.code === 200) {
          this.getSearchData()
          return Promise.resolve(true)
        } else {
          return Promise.resolve(false)
        }
      })
    },
    handleReject (id, content) {
      let params = {
        content: content
      }
      return api.patchApprovalId(id, params).then(res => {
        if (res.code === 200) {
          this.getSearchData()
          return Promise.resolve(true)
        } else {
          return Promise.resolve(false)
        }
      })
    },
    filterKey (arr, key) {
      return arr.map(a => a[key])
    }
  }
}
</script>



